let tip  =  ["", "info", "success", "warning", "danger"];
let icono  =  ["", "ti-info-alt", "ti-check-box", "ti-alert", "ti-announcement"];
let titulo  =  ["", "Aviso", "√âxito", "Advertencia", "Peligro"];

function notifyVue(tipo,mensaje) {
    const parametros = {
        title: titulo[tipo],
        message: mensaje,
        icon: icono[tipo],
        type: tip[tipo]
    };
    return parametros;
};

export default (tipo,mensaje) => {
    const instance = notifyVue(tipo,mensaje);
    return instance;
};