<template>
  <div>
    <div class="row">
      <div class="col-md-6 col-xl-12" v-for="(stats, index) in statsCards" :key="index">
        <stats-card>
          <div class="icon-big text-center" :class="`icon-${stats.type}`" slot="header">
            <i :class="stats.icon"></i>
          </div>
          <div class="icon-big text-center" slot="header">
            <div :class="`alert alert-${stats.type}`">
              <span>{{stats.etap}}</span>
            </div>
            <strong>
              <p>Monto Recaudado: <font color="red" size=5>{{"$"+stats.monto}}</font></p>
            </strong>
          </div>

          <div class="numbers" slot="content">
            <p>Monto Objetivo de la Recaudaci√≥n: {{"$"+stats.value}}</p>
            {{stats.title}}
             <p>{{stats.descr}}</p>
             <p><font color="blue">Cantidad de Inversionista: {{stats.invers}}</font></p>
          </div>

          <div class="numbers" slot="footer">
            <div class="progress">
                <div class="progress-bar" role="progressbar" :style="`width: ${stats.porcentaje}%`" 
                  aria-valuenow="25" aria-valuemin="0" aria-valuemax="100">
                  {{stats.porcentaje}}%</div>
            </div>
          </div>

          <div class="numbers" slot="footer">
            <div class="row">
              <div class="col-3">
                <fg-input type="number" step="0.0001" placeholder="0.0001" 
                  min="0" id="example1" class="form-control" v-model="stats.invertir">
                </fg-input>
                </div>
              <div class="col-2">
                <p-button type="info" @click.native.prevent="Invertir(index)"> Invertir </p-button>   
              </div>
            </div>
          </div>

          <div class="stats" slot="footer">
            <i :class="stats.footerIcon"></i> {{stats.footerIni}}<br/>
            <i :class="stats.footerIcon"></i> {{stats.footerFin}}
       

     
          </div>
        </stats-card>
      </div>
    </div>

{{error}}
  </div>
</template>
<script>

import API from "../plugins/globalApi";
import Mensaje from "../plugins/globalNotificaciones";

import { StatsCard, ChartCard } from "@/components/index";
import Chartist from 'chartist';
export default {
  components: {
    StatsCard,
    ChartCard
  },
  /**
   * Chart data used to render stats, charts. Should be replaced with server data
   */
  data() {
    return {
      correo: '',
      clave: '',
      correo_reg: '',
      clave_reg: '',
      clave_reg2: '',
      error: null, 
      autenticado: false,
      estatus: false,
      statsCards: [],
      etapa: ['En Proceso','Completo','Cerrado'],
      tipo: ['info','success','warning']

    };
  },
  created () { 
    this.Mostrar()
  },
  methods: {
    Mostrar() {
      API({
        action:  'portafolio',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password')
      }).then(response => {
        response.data.portafolio.forEach(element => {
          this.statsCards.push({
            id: element.id,
            type: this.tipo[element.etapa],
            etap: this.etapa[element.etapa],
            icon: "ti-stats-up",
            title: element.titulo,
            descr:  element.descripcion,
            value: element.meta,
            monto: element.monto,
            invertir: 0,
            footerIni: 'Fecha de Inicio: '+element.fecha_ini,
            footerFin: 'Fecha para Finalizar: '+element.fecha_fin,
            invers: element.invers,
            porcentaje: Math.round((element.monto/element.meta)*100),
            footerIcon: "ti-time"
          });
        });
      });

  },
    Invertir(index) {
      if (!this.statsCards[index].invertir) {
        this.$notify(Mensaje(3,'Debe colocar el monto a invertir'));
      } else {
        API({
          action:  'invertir',
          correo:   localStorage.getItem('Login'),
          clave:    localStorage.getItem('Password'),
          portaf:   this.statsCards[index].id,
          monto:   this.statsCards[index].invertir,
        }).then(response => {
            this.error = response.data;
            if (response.data.error){
              this.$notify(Mensaje(3,response.data.message));
            } else {
              this.$notify(Mensaje(2,response.data.message));
              this.statsCards = [];
              this.Mostrar();
            }

        });

      }
    }
  }
};
</script>
