<template>
  <div class="row">
    <div class="col-md-2">
    </div>
    <div class="col-md-8">
      <card class="card" title="Registrarse">
        {{registro.error}}
        <div>
          <form @submit.prevent>
            <div class="row">
              <div class="col-md-6">
                <fg-input type="text"
                      label="Nombre"
                      placeholder="Nombre"
                      v-model="registro.nombre">
                </fg-input>
              </div>
              <div class="col-md-6">
                <fg-input type="text"
                      label="Apellido"
                      placeholder="Apellido"
                      v-model="registro.apellido">
                </fg-input>
              </div>
            </div>
              <div class="row">
              <div class="col-md-12">
                <fg-input type="email"
                      label="Correo"
                      placeholder="Correo Electr√≥nico"
                      v-model="registro.correo">
                </fg-input>
              </div>
            </div>
            <div class="row">
              <div class="col-md-6">
                <fg-input type="password"
                      label="Clave"
                      placeholder="Clave Nueva"
                      v-model="registro.clave1">
                </fg-input>
              </div>
              <div class="col-md-6">
                <fg-input type="password"
                      label="Confirmaci√≥n de Clave"
                      placeholder="Confirmaci√≥n"
                      v-model="registro.clave2">
                </fg-input>
              </div>
            </div>
            <div class="text-center">
              <p-button type="success"
                    round
                    @click.native.prevent="Registrar">
                Registrar
              </p-button>
            </div>
            <div class="clearfix"></div>
          </form>
        </div>
      </card>
    </div>
  </div>
</template>
<script>

import API from "../plugins/globalApi";
import Mensaje from "../plugins/globalNotificaciones";
import Crypto from "crypto-js";

export default {
  data() {
    return {
      registro: {
        correo:   "",
        clave1:   "",
        clave2:   "",
        nombre:   "",
        apellido:   "",
        error:    null,
        validar:  false,
        mens:     null
      }
    };
  },
  created () {

},
  methods: {
    Registrar() {
      if (this.registro.nombre.length==0 || this.registro.apellido.length==0
          ||  this.registro.correo.length==0 || this.registro.clave1.length==0
          || this.registro.clave2.length==0) {

        this.$notify(Mensaje(3,'Debe ingresar los datos solicitados.'));

      } else if (!(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/).test(this.registro.correo)){

        this.$notify(Mensaje(3,'El correo no es v√°lido'));

      } else if (!(/^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})/).test(this.registro.clave1)){

        this.$notify(Mensaje(3,'La clave debe tener una longitud m√≠nima de 8, may√∫sculas, min√≠sculas y un car√°cter especial'));  

      } else if (this.registro.clave1 != this.registro.clave2){

        this.$notify(Mensaje(3,'La confirmaci√≥n de clave es incorrecta'));
        
      } else {
          this.registro.validar = true;

      }

      if (this.registro.validar) {
        API({
          action:  'registrar',
          nombre:   this.registro.nombre,
          apellido: this.registro.apellido,
          correo:   this.registro.correo,
          clave: Crypto.HmacSHA1(this.registro.clave1, "btcoin").toString()
          }).then(response => {
            if (response.data.error){
              this.$notify(Mensaje(3,response.data.message));
            } else {
              this.$notify(Mensaje(2,response.data.message));
            }   
          }).catch(e => {console.log(e);});
          this.registro.clave1 = '';
          this.registro.clave2 = '';
      }
    }
    
  }
} 
</script>