<template>
  <div>
{{error}}
    <!--Stats cards-->
    <div class="row">

      <div class="col-md-6 col-xl-3">
        <stats-card>
          <div class="icon-big text-center icon-warning" slot="header">
            <i class="ti-money"></i>
          </div>
          <div class="numbers" slot="content">
            1 BTC
            <p>${{mercado}}</p>
          </div>
          <div class="stats" slot="footer">
            <i class="fab fa-btc"></i>Cotizaci√≥n de Mercado
          </div>
        </stats-card>
      </div>

      <div class="col-md-6 col-xl-3">
        <stats-card>
          <div class="icon-big text-center icon-success" slot="header">
            <i class="ti-money"></i>
          </div>
          <div class="numbers" slot="content">
            1 BTC
            <p>${{((mercado*(100+parseFloat(porcentaje)))/100).toFixed(4)}}</p>
          </div>
          <div class="stats" slot="footer">
            <i class="fab fa-btc"></i>Valor BTC + {{porcentaje}}%
          </div>
        </stats-card>
      </div>

      <div class="col-md-6 col-xl-3">
        <stats-card>
          <div class="icon-big text-center" slot="header">
            <img src="https://image.flaticon.com/icons/png/512/25/25180.png" width="50">
          </div>
          <div class="numbers" slot="content">
            BTC
            <p>{{balance}}</p>
          </div>
          <div class="stats" slot="footer">
            <i class="fab fa-btc"></i>Cuenta Maestra 
              ${{(balance*((mercado*(100+parseFloat(porcentaje)))/100)).toFixed(2)}}
          </div>
        </stats-card>
      </div>

      <div class="col-md-6 col-xl-3">
        <stats-card>
          <div class="icon-big text-center" slot="header">
            <img src="https://www.iconfinder.com/data/icons/bitcoin-and-cryptocurrency-2-2/50/185-512.png" width="50">
          </div>
          <div class="numbers" slot="content">
            {{transactions}}
          </div>
          <div class="stats" slot="footer">
              Transacciones BTC
          </div>
        </stats-card>
      </div>


    </div>

    <div class="row">
      <div class="col-12">
         <card class="card" title="Configuraci√≥n para Transacciones BTC">     
          <div class="row" >
            <div class="col-md-10">
              <fg-input type="text" label="Private Key (Wallet Import Format)" 
              v-model="WIF" :disabled="disabled == true">
              </fg-input>
            </div>
            <div class="col-md-2">
              <fg-input type="number" step="0.01" placeholder="0.01" label="% BTC"
                  min="0" max="100" v-model="porcentaje" :disabled="disabled == true">
              </fg-input>
            </div>
            <div class="col-md-10">
              <fg-input type="text" label="Address" 
              v-model="wallet" disabled>
              </fg-input>
            </div>
            <div class="col-md-2">
              <fg-input type="number" step="0.01" placeholder="0.01" label="% Comisi√≥n"
                  min="0" max="100" v-model="comision" :disabled="disabled == true">
              </fg-input>
            </div>
            <div class="col-md-1">
                <button type="button"  class="close"  @click="Movimientos()">
                    <img src="https://www.iconfinder.com/data/icons/bitcoin-and-cryptocurrency-2-2/50/185-512.png" width="40">
                </button>
            </div>
            <div class="col-md-1">
                <button type="button"  class="close"  @click="VerUtxos()">
                    utxos
                </button>
            </div>
            <div class="col-md-10" v-if="!disabled">
                <button type="button" aria-hidden="true" class="close" @click="Guardar()">
                  <i class="ti-save"></i>
                </button>
            </div>
            <div class="col-md-11" v-if="disabled">
                <button type="button" aria-hidden="true" class="close" @click="Editar()">
                  <i class="ti-pencil"></i>
                </button>
            </div>
            <div class="col-md-1" v-if="!disabled">
                <button type="button"  class="close"  @click="Cerrar()">
                  <i class="ti-close"></i>
                </button>
            </div>

          </div>
         </card>
      </div>

      <div class="col-12" v-if="verutxo">
        <card :title="table1.title" :subTitle="table1.subTitle">
          <div slot="raw-content" class="table-responsive">
          <table class="table" :class="tableClass">
            <thead>
              <slot name="columns">
                <th v-for="column in table1.columns" :key="column">{{column}}</th>
              </slot>
            </thead>
            <tbody>
              <tr v-for="(item, index) in table1.data" :key="index">
                <slot :row="item">
              <td v-for="(column, index) in table1.columns"
                :key="index"
                v-if="hasValue(item, column)">
                {{itemValue(item, column)}}
              </td>        
                </slot>
              </tr>
            </tbody>
          </table>
          </div>
        </card>
      </div>


      <div class="col-12" v-if="vertrans">
        <card :title="table2.title">
          <div slot="raw-content" class="table-responsive">
          <table class="table" :class="tableClass">
            <thead>
              <slot name="columns">
                <th v-for="column in table2.columns" :key="column">{{column}}</th>
              </slot>
            </thead>
            <tbody>
              <tr v-for="(item, index) in table2.data" :key="index">
                <slot :row="item">
              <td v-for="(column, index) in table2.columns"
                :key="index"
                v-if="hasValue(item, column)">
                {{itemValue(item, column)}}
              </td>        
                </slot>
              </tr>
            </tbody>
          </table>
          </div>
        </card>
      </div>


    </div>






    </div>
  </div>
</template>
<script>
import API from "../plugins/globalApi";
import Mensaje from "../plugins/globalNotificaciones";
import axios from "axios";
import CoinKey from "coinkey";
import ci from "coininfo";
import * as secure from "securejs";
import BT from "../plugins/Bitcoin";
const Bitcoin = new BT('testnet');

import { StatsCard, ChartCard } from "@/components/index";
import Chartist from 'chartist';
export default {
  components: {
    StatsCard,
    ChartCard
  },

  data() {
    return {
      error:    null,
      mercado: 0,
      porcentaje: 0,
      comision: 0,
      wallet: null,
      balance: 0,
      WIF: null,
      Hex: null,
      disabled: true,
      hash: null,
      valor: null,
      transactions: 0,
      vertrans: false,
      verutxo: false,
      utxos: null,
      table1: {
        title: "Utxos",
        subTitle: "",
        columns: ["Hash","Vout","Satoshis","Confirmed"],
        data: [],
        edicion: true
      },
      table2: {
        title: "Transacciones",
        subTitle: "",
        columns: ["Hash","Valor","Confirmed"],
        data: [],
        edicion: true
      },

    };
  },
  created () { 
    if(localStorage.getItem('rol')!=2){
      window.location.href = '#/inicio';
    }
    this.Mostrar();
  },
  computed: {
    tableClass() {
      return `table-${this.type}`;
    }
  },
  methods: {
    Mostrar() {
      this.error =  null;
      this.wallet =  null;
      this.balance =  0;
      this.porcentaje =  0;
      this.comision =  0;
      this. WIF =  null;
      this.Hex =  null;
      this.disabled =  true;
      this.hash = null;
      this.valor = null;
      this.utxos = null;
      this.transactions = 0;
      this.table1.data = [];
      API({
        action:  'configuracion',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password')
      }).then(response => {
        this.porcentaje = response.data.configuracion.porcentaje;
        this.comision = response.data.configuracion.comision;
        const decWIF = secure.decrypt(response.data.configuracion.wif, 'btcoin');
        this.WIF = decWIF;
        Bitcoin.privateKeyToAccount(decWIF);
        this.wallet = Bitcoin.defaultAccount.address;
        Bitcoin.currentprice().then(result => {
          const btc = parseFloat((result.bpi.USD.rate).replace(',', '')).toFixed(4)
          this.mercado  = btc;
        });
        Bitcoin.getBalance().then(result => {
          this.transactions = result.chain_stats['tx_count']+ result.mempool_stats['tx_count'];
          const received = result.chain_stats['funded_txo_sum'];
          const spent = result.chain_stats['spent_txo_sum'];
          this.balance = ((received-spent)/100000000).toFixed(8);
        });
        Bitcoin.getUtxo().then(result => {
          let disp = 0;
          for (let i = 0; i < result.length; i++) {
            if (result[i]['confirmed']) {
              disp += result[i]['satoshis'];
            }
          }
          this.table1.subTitle = 'Disponible: ' + disp + ' Satoshis';
          result.forEach(element => {
            this.table1.data.push({
              hash: element['txid'],
              vout: element['vout'],
              satoshis: element['satoshis'],
              confirmed: element['confirmed'],         
            });
          });
        });
        Bitcoin.getTxs().then(result => {
          this.table2.subTitle = "Cantidad de Transacciones: " + result.length;          
          result.forEach(element => {
            let value = 0;
            for(let i = 0; i < element['vout'].length; i++){
              value = value + element['vout'][i]['value'];
            }
            this.table2.data.push({
              hash: element['txid'],
              valor: (value/100000000).toFixed(8) + ' BTC',
              confirmed: element["status"]["confirmed"],
            });
          });
        });
      });
    },
    Editar() {
      this.disabled = false;
    },
    Guardar() {
      API({
        action:  'updateconfiguracion',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password'),
        wif: secure.encrypt(this.WIF, 'btcoin'),
        porcentaje: this.porcentaje,
        comision: this.comision
      }).then(response => {
        if (response.data.error){
          this.$notify(Mensaje(3,response.data.message));
        } else {
          this.$notify(Mensaje(2,response.data.message));
        }   
      });
      this.disabled = true;
      this.Mostrar();
    },
    Cerrar() {
      this.disabled = true;
    },
    Movimientos() {
      this.vertrans = !this.vertrans;
    },
    VerUtxos() {
      this.verutxo = !this.verutxo;
    },
    Generar() {
      //const ck = new CoinKey.createRandom();
      //TESNET
      const ck = CoinKey.createRandom(ci('BTC-TEST'))
      this.wallet = ck.publicAddress;
      this.WIF = ck.privateWif;
      this.Hex = ck.privateKey.toString('hex');
    },
    hasValue(item, column) {
      return item[column.toLowerCase()] !== "undefined";
    },
    itemValue(item, column) {
      return item[column.toLowerCase()];
    },
  }
};
</script>
<style>
</style>
