<template>
  <div>
{{error}}
    <!--Stats cards-->
    <div class="row">
      <div class="col-md-6 col-xl-4">
        <stats-card>
          <div class="icon-big text-center icon-warning" slot="header">
            <img src="https://coinlist.me/wp-content/uploads/2017/11/bitcoin-dollars-exchange.png" width="50">
          </div>
          <div class="numbers" slot="content">
            1 BTC
            <p>${{mercado}}</p>
          </div>
          <div class="stats" slot="footer">
            <i class="fab fa-btc"></i>Cotizaci√≥n de Intercambio
          </div>
        </stats-card>
      </div>

      <div class="col-md-6 col-xl-4">
        <stats-card>
          <div class="icon-big text-center icon-info" slot="header">
            <i class="ti ti-money"></i>
          </div>
          <div class="numbers" slot="content">
            Saldo
            <p>${{saldo}}</p>
          </div>
          <div class="stats" slot="footer">
            Valor en BTC {{(saldo/mercado).toFixed(4)}}
          </div>
        </stats-card>
      </div>


      <div class="col-md-6 col-xl-4">
        <stats-card>
          <div class="icon-big text-center" slot="header">
            <img src="https://image.flaticon.com/icons/png/512/25/25180.png" width="50">
          </div>
          <div class="numbers" slot="content">
            BTC
            <p>{{balance}}</p>
          </div>
          <div class="stats" slot="footer">
            <i class="fab fa-btc"></i>Saldo de Mi Wallet
          </div>
        </stats-card>
      </div>


    </div>

    <div class="row">
      <div class="col-8">
         <card class="card" title="Mi Wallet BTC">     
          <div class="row" >
            <div class="col-md-12">
              <fg-input type="text" label="Address" v-model="wallet" :disabled="disabled == true">
              </fg-input>
            </div>
            <div class="col-md-12">
              <fg-input type="text" label="Private Key (Wallet Import Format)" v-model="WIF" disabled>
              </fg-input>
            </div>
            <div class="col-md-12">
              <fg-input type="text" label="Private Key (Hex)" v-model="Hex" disabled>
              </fg-input>
            </div>
            <div class="col-md-2">
                <button type="button"  class="close"  @click="Movimientos()">
                    <img src="https://www.iconfinder.com/data/icons/bitcoin-and-cryptocurrency-2-2/50/185-512.png" width="40">
                </button>
            </div>
            <div class="col-md-8" v-if="!disabled">
                <button type="button" aria-hidden="true" class="close" @click="Guardar()">
                  <i class="ti-save"></i>
                </button>
            </div>
            <div class="col-md-10" v-if="disabled">
                <button type="button" aria-hidden="true" class="close" @click="Editar()">
                  <i class="ti-pencil"></i>
                </button>
            </div>
            <div class="col-md-1" v-if="!disabled">
                <button type="button"  class="close"  @click="Generar()">
                  <i class="ti-reload"></i>
                </button>
            </div>
            <div class="col-md-1" v-if="!disabled">
                <button type="button"  class="close"  @click="Cerrar()">
                  <i class="ti-close"></i>
                </button>
            </div>
          </div>
         </card>



    </div>
    <div class="col-4">
    <div class="row">
      <div class="col-md-6 col-xl-12">
        <stats-card>
          <div class="icon-big text-center" slot="header">
            <img src="https://www.iconfinder.com/data/icons/bitcoin-and-cryptocurrency-2-2/50/185-512.png" width="70">
          </div>
          <div class="numbers" slot="content">
            <p>Transacci√≥n</p>
            <p>{{table1.data[0].valor}}</p>
          </div>
          <div class="stats" slot="footer">
              {{table1.data[0].hash}} 
          </div>
        </stats-card>
      </div>
      <div class="col-md-6 col-xl-12">
        <stats-card>
          <div class="icon-big text-center" slot="header">
            <img src="https://www.iconfinder.com/data/icons/bitcoin-and-cryptocurrency-2-2/50/185-512.png" width="70">
          </div>
          <div class="numbers" slot="content">
            <p>Transacci√≥n</p>
            <p>{{table1.data[1].valor}}</p>
          </div>
          <div class="stats" slot="footer">
              {{table1.data[1].hash}} 
          </div>
        </stats-card>
      </div>
    </div>
    </div>
    
          <div class="col-12" v-if="vertrans">
        <card :title="table1.title" :subTitle="table1.subTitle">
          <div slot="raw-content" class="table-responsive">
          <table class="table" :class="tableClass">
            <thead>
              <slot name="columns">
                <th v-for="column in table1.columns" :key="column">{{column}}</th>
              </slot>
            </thead>
            <tbody>
              <tr v-for="(item, index) in table1.data" :key="index">
                <slot :row="item">
              <td v-for="(column, index) in table1.columns"
                :key="index"
                v-if="hasValue(item, column)">
                {{itemValue(item, column)}}
              </td>        
                </slot>
              </tr>
            </tbody>
          </table>
          </div>
        </card>
      </div>

    
    
    </div>

  </div>
</template>
<script>
import API from "../plugins/globalApi";
import Mensaje from "../plugins/globalNotificaciones";
import axios from "axios";
import CoinKey from "coinkey";
import ci from "coininfo";
import * as secure from "securejs";
import BT from "../plugins/Bitcoin";
const Bitcoin = new BT('testnet');

import { StatsCard, ChartCard } from "@/components/index";
import Chartist from 'chartist';
export default {
  components: {
    StatsCard,
    ChartCard
  },
  /**
   * Chart data used to render stats, charts. Should be replaced with server data
   */
  data() {
    return {
      error:    null,
      wallet: null,
      saldo: 0,
      mercado: 0,
      balance: 0,
      WIF: null,
      Hex: null,
      disabled: true,
      valor: null,
      vertrans: false,
      table1: {
        title: "Transacciones",
        subTitle: "Cantidad de Transacciones: 0",
        columns: ["Hash","Valor","Confirmed"],
        data: [],
        edicion: true
      },

    };
  },
  created () { 
    this.Mostrar();
  },
  computed: {
    tableClass() {
      return `table-${this.type}`;
    }
  },
  methods: {
    Mostrar() {
      this.error =  null;
      this.wallet =  null;
      this.balance =  0;
      this. WIF =  null;
      this.Hex =  null;
      this.disabled =  true;
      this.hash = null;
      this.valor = null;
      this.saldo = 0;
      this.transactions = [];

      API({
        action:  'informacion',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password')
      }).then(response => {
        const user = response.data.user;
        this.wallet = user.wallet;
        this.WIF = secure.decrypt(user.wif, 'btcoin');
        this.Hex = secure.decrypt(user.hex, 'btcoin');
        this.saldo = response.data.saldo;
        const porcentaje = parseFloat(response.data.configuracion.porcentaje);
        Bitcoin.currentprice().then(result => {
          const btc = parseFloat((result.bpi.USD.rate).replace(',', ''));
          this.mercado  = ((btc*(100+porcentaje))/100).toFixed(4);
        });
        Bitcoin.getBalance(this.wallet).then(result => {
          this.transactions = result.chain_stats['tx_count']+ result.mempool_stats['tx_count'];
          const received = result.chain_stats['funded_txo_sum'];
          const spent = result.chain_stats['spent_txo_sum'];
          this.balance = ((received-spent)/100000000).toFixed(8);
        });
        Bitcoin.getTxs(this.wallet).then(result => {
          this.table1.subTitle = "Cantidad de Transacciones: " + result.length;          
          result.forEach(element => {
            let value = 0;
            for(let i = 0; i < element['vout'].length; i++){
              value = value + element['vout'][i]['value'];
            }
            this.table1.data.push({
              hash: element['txid'],
              valor: (value/100000000).toFixed(8) + ' BTC',
              confirmed: element["status"]["confirmed"]
            });
          });
        });
      });
    },
    Editar() {
      this.WIF = '-';
      this.Hex = '-';
      this.disabled = false;
    },
    Guardar() {
      const securewif = secure.encrypt(this.WIF, 'btcoin');
      const securehex = secure.encrypt(this.Hex, 'btcoin');
      API({
        action:  'updatewallet',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password'),
        wallet: this.wallet,
        wif: securewif,
        hex: securehex
      }).then(response => {
        if (response.data.error){
          this.$notify(Mensaje(3,response.data.message));
        } else {
          this.$notify(Mensaje(2,response.data.message));
        }   
      });
      this.disabled = true;
      this.Mostrar();
    },
    Cerrar() {
      this.disabled = true;
      this.Mostrar();
    },
    Generar() {
      //const ck = new CoinKey.createRandom();
      //TESNET
      const ck = CoinKey.createRandom(ci('BTC-TEST'))
      this.wallet = ck.publicAddress;
      this.WIF = ck.privateWif;
      this.Hex = ck.privateKey.toString('hex');
    },
    hasValue(item, column) {
      return item[column.toLowerCase()] !== "undefined";
    },
    itemValue(item, column) {
      return item[column.toLowerCase()];
    },
    Movimientos() {
      this.vertrans = !this.vertrans;
    },


  }
};
</script>
