<template>
  <div>
    {{error}}
    <!--Table-->
    <div class="row" v-if="!modificar.activo">
      
      <div class="col-12">
        <card :title="table1.title">
          <div slot="raw-content" class="table-responsive">
            <div class="col-md-12">
              <button type="button"  class="close"  @click="Editar()">
                <i class="ti-write"></i>
              </button>
            </div>

          <table class="table" :class="tableClass">
            <thead>
              <slot name="columns">
                <th v-for="column in table1.columns" :key="column">{{column}}</th>
              </slot>
            </thead>
            <tbody>
              <tr v-for="(item, index) in table1.data" :key="index">
                <slot :row="item">
              <td v-for="(column, index) in table1.columns"
                :key="index"
                v-if="hasValue(item, column)">
                {{itemValue(item, column)}}
              </td>        
              <td>
                <button type="button" aria-hidden="true" class="close" @click="Editar(index)">
                  <i class="ti-pencil"></i>
                </button>
              </td>
                </slot>
              </tr>
            </tbody>
          </table>
          </div>
        </card>
      </div>
    </div>

    <div class="row" v-else>
      <div class="col-12">
        <card class="card" title="Modificar Usuario">           
          <div class="row" >
            <div class="col-md-12">
              <button type="button"  class="close"  @click="Cerrar()">
                <i class="ti-close"></i>
              </button>
            </div>
          </div>

          <form @submit.prevent>

          <div class="row">
            <div class="col-md-6">
              <fg-input type="text" label="Apellido" v-model="modificar.apellido">
              </fg-input>
            </div>
            <div class="col-md-6">
              <fg-input type="text" label="Nombre" v-model="modificar.nombre">
              </fg-input>
            </div>
          </div>

          <div class="row">
            <div class="col-md-6">
              <fg-input type="email" label="Correo" v-model="modificar.correo">
              </fg-input>
            </div>
            <div class="col-md-3">
              <label>Rol</label>
              <select v-model="modificar.rol" class="form-control border-input">
                <option v-for="(item, key) in roles" :value="key" >
                    {{item}}
                </option>
              </select>
            </div>
          </div>

          </form>

          <div class="row" >
            <div class="col-md-12">
              <button type="button"  class="close"  @click="Guardar()">
                <i class="ti-save"></i>
              </button>
            </div>
          </div>


        </card>
      </div>
    </div>


  </div>
</template>
<script>
import API from "../plugins/globalApi";
import Mensaje from "../plugins/globalNotificaciones";

import { StatsCard, ChartCard } from "@/components/index";
import Chartist from 'chartist';

export default {
  components: {
    StatsCard,
    ChartCard
  },
  /**
   * Chart data used to render stats, charts. Should be replaced with server data
   */
  data() {
    return {
      error: null,
      table1: {
        title: "Usuarios Registrados",
        subTitle: "Registros",
        columns: ["Id", "Apellido", "Nombre", "Correo", "Rol", "Fecha"],
        data: [],
        edicion: true
      },
      modificar: {
        activo: false,
        id: 0,
        apellido: null,
        nombre: null,
        correo: null,
        rol: null
      },
      roles: ['B√°sico','Gestor','Administrador']
    }
  },
  created () { 
    if(localStorage.getItem('rol')!=2){
      window.location.href = '#/inicio';
    }
    this.Mostrar();
  },
  computed: {
    tableClass() {
      return `table-${this.type}`;
    }
  },
  methods: {
    Mostrar() {
      API({
        action:  'usuarios',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password')
      }).then(response => {
        //this.error = response.data.usuario;
        response.data.usuario.forEach(element => {
          this.table1.data.push({
            id: element.id,
            apellido: element.apellido,
            nombre: element.nombre,
            correo: element.correo,
            rol: this.roles[element.rol],
            rolid: element.rol,
            fecha: element.fecha
          });
        });
      });
    },
    hasValue(item, column) {
      return item[column.toLowerCase()] !== "undefined";
    },
    itemValue(item, column) {
      return item[column.toLowerCase()];
    },
    Editar(index) {
      this.modificar.activo = true;
      this.modificar.id = this.table1.data[index].id;
      this.modificar.apellido = this.table1.data[index].apellido;
      this.modificar.nombre = this.table1.data[index].nombre;
      this.modificar.correo = this.table1.data[index].correo;
      this.modificar.rol = this.table1.data[index].rolid;
    },
    Guardar() {
      API({
        action:  'updateusuario',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password'),
        id: this.modificar.id,
        apellido: this.modificar.apellido,
        nombre: this.modificar.nombre,
        correo2: this.modificar.correo,
        rol: this.modificar.rol
      }).then(response => {
        //this.error = response.data;
        if (response.data.error){
          this.$notify(Mensaje(3,response.data.message));
        } else {
          this.$notify(Mensaje(2,response.data.message));
          this.table1.data = [];
          this.Mostrar();
        }   
      });
    },
    Cerrar() {
      this.modificar.activo = false;
      this.modificar.id = 0;
      this.modificar.apellido = null;
      this.modificar.nombre = null;
      this.modificar.correo = null;
      this.modificar.gestor = 0;
      this.modificar.admin = 0;
    }



    
  }
};
</script>
