<template>
  <div class="row">
    <div class="col-md-3">
    </div>
    <div class="col-md-6">

      <card class="card" title="Ingresar">
        {{this.login.error}}
        <div>
          <form @submit.prevent>
            <div class="row">
              <div class="col-md-12">
                <fg-input type="email"
                      label="Correo"
                      placeholder="Correo Electr√≥nico"
                      v-model="login.correo">
                </fg-input>
              </div>
            </div>
            <div class="row">
              <div class="col-md-12">
                <fg-input type="password"
                      label="Clave"
                      placeholder="Clave"
                      v-model="login.clave">
                </fg-input>
              </div>
            </div>
            <div class="text-center">
              <p-button type="success"
                    round
                    @click.native.prevent="Ingresar">
                Ingresar
              </p-button>
            </div>
           <div class="clearfix"></div>
          </form>
        </div>
      </card>
    </div>
  </div>
  
</template>
<script>

import API from "../plugins/globalApi";
import Mensaje from "../plugins/globalNotificaciones";
import Crypto from "crypto-js";

export default {
  data() {
    return {
      login: {
        correo: null,
        clave: null,
        error:    null,
        validar:  false,
        mens:     null
      }
    }
  },
  created () {
    if(localStorage.getItem('Verificacion')){
      window.location.href = '#/inicio';
    }

  },
  methods: {
    Ingresar() {
      if (this.login.correo.length==null || this.login.clave.length==null) {
        this.$notify(Mensaje(3,'Debe ingresar los datos solicitados.'));
      } else if (!(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/).test(this.login.correo)){
        this.$notify(Mensaje(3,'El correo no es v√°lido'));
      } else {
        this.login.validar = true;
      }

      if (this.login.validar) {
        const cifrado = Crypto.HmacSHA1(this.login.clave, "btcoin").toString();
        API({       
          action:  'login',
          correo:   this.login.correo,
          clave: cifrado
          }).then(response => {
            //this.login.error = response.data.user;
            if (response.data.error){
              this.$notify(Mensaje(3,response.data.message));
            } else {
              this.$notify(Mensaje(2,response.data.message));
              localStorage.setItem('Login', this.login.correo);
              localStorage.setItem('Password', cifrado);
              localStorage.setItem('User',response.data.user.nombre+' '+response.data.user.apellido);
              localStorage.setItem('Verificacion', response.data.authenticated);
              localStorage.setItem('rol', response.data.user.rol);
              this.login.correo = '';
              this.login.clave = '';
              this.$router.go(0);
            }   
          }).catch(e => {console.log(e);})




       }
    }
  }
}
</script>