<template>
  <div>
{{error}}
    <!--Stats cards-->
    <div class="row">
      <div class="col-md-6 col-xl-4">
        <stats-card>
          <div class="icon-big text-center icon-warning" slot="header">
            <img src="https://coinlist.me/wp-content/uploads/2017/11/bitcoin-dollars-exchange.png" width="50">
          </div>
          <div class="numbers" slot="content">
            1 BTC
            <p>${{mercado}}</p>
          </div>
          <div class="stats" slot="footer">
            <i class="fab fa-btc"></i>Cotizaci√≥n de Intercambio
          </div>
        </stats-card>
      </div>

      <div class="col-md-6 col-xl-4">
        <stats-card>
          <div class="icon-big text-center icon-info" slot="header">
            <i class="ti ti-money"></i>
          </div>
          <div class="numbers" slot="content">
            Saldo
            <p>${{saldo}}</p>
          </div>
          <div class="stats" slot="footer">
            Valor en BTC {{(saldo/mercado).toFixed(8)}}
          </div>
        </stats-card>
      </div>

      <div class="col-md-6 col-xl-4">
        <stats-card>
          <div class="icon-big text-center" slot="header">
            <img src="https://image.flaticon.com/icons/png/512/25/25180.png" width="50">
          </div>
          <div class="numbers" slot="content">
            BTC
            <p>{{balance}}</p>
          </div>
          <div class="stats" slot="footer">
            <i class="fab fa-btc"></i>Saldo de Mi Wallet
          </div>
        </stats-card>
      </div>

    </div>

    <div class="row">
      <div class="col-12">
         <card class="card">     
          <div class="row" >

            <div class="col-md-4">
                <button type="button"  class="close"  @click="Caso(1)">
                   Enviar Dinero
                    <img src="https://upload.wikimedia.org/wikipedia/commons/0/0c/Message_%28Send%29.png" width="60">
                </button>               
            </div>

            <div class="col-md-4">
                <button type="button"  class="close"  @click="Caso(2)">
                    Intercambio BTC
                    <img src="https://marketing4food.com/wp-content/uploads/2020/02/bitcoin-e1397127758581.png" width="60">
                </button>
            </div>

            <div class="col-md-4">
                <button type="button"  class="close"  @click="Caso(3)">
                    Reporte
                    <img src="https://diarioresponsable.com/images/stories/Reporte_I.png" width="60">
                </button>
            </div>

          </div>
         </card>
    </div>

    <div class="col-12" v-if="caso == 1">
      <card class="card" title="Enviar Dinero">     
          <div class="row" >
            <div class="col-md-8">
              <fg-input type="text" label="Correo Receptor" v-model="recibe">
              </fg-input>
            </div>
            <div class="col-md-2">
              <fg-input type="number" step="0.01" placeholder="0.01" label="Monto $"
                  min="0" :max="saldo" v-model="montodolar">
              </fg-input>
            </div>
            <div class="col-md-1">
            <div class="text-center">
              <p-button type="success"
                    round
                    @click.native.prevent="Enviar">
                Procesar
              </p-button>
            </div>
            </div>
          </div>
      </card>
    </div>

    <div class="col-12" v-if="caso == 2">
      <card class="card" title="Intercambio a BTC">     
        <div class="row" >
          <div class="col-md-10">
            <div class="row" >
              <div class="col-md-9">
                <fg-input type="text" label="Wallet de Recepci√≥n" v-model="wallet" disabled>
                </fg-input>
              </div>
              <div class="col-md-3">
                <fg-input type="number" step="0.0001"  label="Monto BTC"
                  min="0" v-bind:value="((dolar/mercado).toFixed(8))" disabled>
                </fg-input>
              </div>
              <div class="col-md-3">
                <fg-input type="text"  label="Monto Satoshis"
                  min="0" v-bind:value="((100000000*dolar/mercado).toFixed(0))" disabled>
                </fg-input>
              </div>
              <div class="col-md-3">
                <fg-input type="number" step="0.01" label="Comisi√≥n $"
                  min="0" v-bind:value="VerComision('total')" disabled >
                </fg-input>
              </div>
              <div class="col-md-3">
                <label>Tarifa</label>
                <select v-model="tarifa" class="form-control border-input">
                  <option v-for="(item, key) in fees.nombre" :value="key" >
                    {{item}}
                  </option>
                </select>
              </div>

              <div class="col-md-3">
                <fg-input type="number" step="0.01"  label="Monto $"
                  min="0" v-bind:max="saldo" v-model="dolar" >
                </fg-input>
              </div>

            </div>
          </div>
          <div class="col-md-2">
            <div class="row" >
              <div class="col-md-12">
                <div class="text-center" v-if="disponibilidad">
                  <p-button type="success" round @click.native.prevent="Cambiar">
                    Procesar
                  </p-button>
                </div>
              </div>
            </div>
          </div>
        </div>

        
        
      </card>
    </div>

    <div class="col-12" v-if="caso == 3">
        <card :title="table1.title">
          <div slot="raw-content" class="table-responsive">
          <table class="table" :class="tableClass">
            <thead>
              <slot name="columns">
                <th v-for="column in table1.columns" :key="column">{{column}}</th>
              </slot>
            </thead>
            <tbody>
              <tr v-for="(item, index) in table1.data" :key="index">
                <slot :row="item">
              <td v-for="(column, index) in table1.columns"
                :key="index"
                v-if="hasValue(item, column)">
                {{itemValue(item, column)}}
              </td>        
                </slot>
              </tr>
            </tbody>
          </table>
          </div>
        </card>
    </div>
  
  </div>

  </div>
</template>
<script>
import API from "../plugins/globalApi";
import Mensaje from "../plugins/globalNotificaciones";
import axios from "axios";
import CoinKey from "coinkey";
import ci from "coininfo";
import * as secure from "securejs";
import BT from "../plugins/Bitcoin";
const Bitcoin = new BT('testnet');


import { StatsCard, ChartCard } from "@/components/index";
import Chartist from 'chartist';
export default {
  components: {
    StatsCard,
    ChartCard
  },
  data() {
    return {
      error:    null,
      wallet: null,
      saldo: 0,
      mercado: 0,
      balance: 0,
      WIF: null,
      Hex: null,
      disabled: true,
      hash: null,
      valor: null,
      transactions: null,
      caso: 0,
      recibe: null,
      montodolar: 0,
      dolar: 0,
      montobtc: 0,
      decWIF: null,
      tarifa: 0,
      fees: {
        nombre: ["Baja","Media","Alta"],
        tipo:   ["hourFee","halfHourFee","fastestFee"],
        valor:  [0,0,0]
      },
      comision: 0,
      comis: 0,
      comisiontotal: 0,
      porcentaje: 0,
      utxos: null,
      table1: {
        title: "Reporte de Transacciones",
        subTitle: "",
        columns: ["Fecha","Descripci√≥n","Monto"],
        data: [],
        edicion: true
      },

    };
  },
  created () { 
    this.Mostrar();

  },
  computed: {
    tableClass() {
      return `table-${this.type}`;
    },
  },
  methods: {
    Mostrar() {
      this.error =  null;
      this.wallet =  null;
      this.balance =  0;
      this.WIF =  null;
      this.Hex =  null;
      this.disabled =  true;
      this.hash = null;
      this.valor = null;
      this.saldo = 0;
      this.transactions = null;
      this.decWIF = null;
      this.comision = 0;
      this.comis = 0;
      this.comisiontotal = 0;
      this.porcentaje = 0;
      this.disponibilidad = true;
      API({
        action:  'informacion',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password')
      }).then(response => {
        const user = response.data.user;
        this.wallet = user.wallet;
        this.WIF = secure.decrypt(user.wif, 'btcoin');
        this.Hex = secure.decrypt(user.hex, 'btcoin');
        this.saldo = parseFloat(response.data.saldo);
        this.porcentaje = parseFloat(response.data.configuracion.porcentaje);
        this.comision = parseFloat(response.data.configuracion.comision);
        this.decWIF = response.data.configuracion.wif;
        Bitcoin.privateKeyToAccount(secure.decrypt(response.data.configuracion.wif, 'btcoin'));
        Bitcoin.getUtxo().then(result => {
          this.utxos = result;
        });
        response.data.reporte.forEach(element => {
          this.table1.data.push({
            fecha: element['fecha'],
            descripci√≥n: element['descripcion'],
            monto: "$ "+(element['monto']*element['tipo']).toFixed(4),
          });
        });
        Bitcoin.currentprice().then(result => {
          const btc = parseFloat((result.bpi.USD.rate).replace(',', ''));
          this.mercado  = ((btc*(100+this.porcentaje))/100).toFixed(4);
        });
        Bitcoin.getBalance(this.wallet).then(result => {
          this.transactions = result.chain_stats['tx_count']+ result.mempool_stats['tx_count'];
          const received = result.chain_stats['funded_txo_sum'];
          const spent = result.chain_stats['spent_txo_sum'];
          this.balance = ((received-spent)/100000000).toFixed(8);
        });
        Bitcoin.estimateFee().then(result => {
          this.fees.valor[0] = result.hourFee;
          this.fees.valor[1] = result.halfHourFee;
          this.fees.valor[2] = result.fastestFee;
        });
      });
    },
    Enviar() {
      if (this.recibe==null) {
        this.$notify(Mensaje(3,'Debe ingresar los datos solicitados.'));
      } else if (this.montodolar==0) {
        this.$notify(Mensaje(3,'Debe ingresar un monto a enviar.'));
      } else if (!(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/).test(this.recibe)){
        this.$notify(Mensaje(3,'El correo no es v√°lido.'));
      } else {
      API({
        action:  'enviardinero',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password'),
        send: this.recibe,
        monto: this.montodolar
      }).then(response => {
        if (response.data.error){
          this.$notify(Mensaje(3,response.data.message));
          this.saldo = 0;
          this.Mostrar();
        } else {
          this.$notify(Mensaje(2,response.data.message));
          this.saldo = 0;
          this.Mostrar();
        }   
      });
      }
    },
    Cambiar() {
      if (this.dolar==0 || this.dolar>this.saldo) {
        this.$notify(Mensaje(3,'Debe ingresar un monto a Intercambiar.'));
      } else {
       this.CalculoMercado();
        const tipofee = this.fees.tipo[this.tarifa];
        const resultfee = this.VerComision('fee');
        const comistotal = this.VerComision('total');
        const comist = this.VerComision('comision');
        const satoshi = parseFloat(resultfee) + parseFloat((100000000*(this.dolar/this.mercado)).toFixed(0)); 
        const fee_uso = parseFloat((100000000*(comist/this.mercado)).toFixed(0)); 
        Bitcoin.sendTransaction(this.wallet,satoshi,tipofee).then(result => {
          if (result.success){            
            API({
              action:   'intercambiar',
              correo:   localStorage.getItem('Login'),
              clave:    localStorage.getItem('Password'),
              fees:     resultfee,
              satoshi:  satoshi,
              txhash:   result.txid,
              mercado:  this.mercado,
              monto_total:    parseFloat(this.dolar)+parseFloat(comistotal),
              origen:     Bitcoin.defaultAccount.address,
              destino:     this.wallet,
              monto_cambiar:    parseFloat(this.dolar),
              fee_uso: comist,
              price_spread: parseFloat(this.dolar)-parseFloat(comist),
              fee_red: fee_uso
            }).then(response => {
              if (response.data.error){
                this.$notify(Mensaje(3,response.data.message));
              } else {
                this.$notify(Mensaje(2,response.data.message));
              }   
            });
          } else {
            this.$notify(Mensaje(3,'Disculpe, Intente mas tarde.')); 
          }
          this.saldo = 0;
          this.Mostrar();
        }).catch(err => {
          this.$notify(Mensaje(3,'Disculpe, Intente mas tarde.')); 
        });
      } 
    },
    hasValue(item, column) {
      return item[column.toLowerCase()] !== "undefined";
    },
    itemValue(item, column) {
      return item[column.toLowerCase()];
    },
    Caso(index) {
      this.caso = index;
    },
    getTxSize(vin, vout) {
      return vin * 180 + vout * 34 + 10 + vin;
    },
    VerComision(caso) {
      this.CalculoMercado();
      const value = (100000000*this.dolar/this.mercado).toFixed(0);
      const feePerByte = this.fees.valor[this.tarifa];
      let ninputs = 0;
      let availableSat = 0;
      for (let i = 0; i < this.utxos.length; i++) {
        if (this.utxos[i]['confirmed']) {
          availableSat += this.utxos[i].['satoshis'];
          ninputs++;
          if (availableSat >= value) break
        }
      }
      if (availableSat < value) {
          this.disponibilidad=false;
      } else {
        this.disponibilidad=true;
        const change = availableSat - value;
        const fee = this.getTxSize(ninputs, change > 0 ? 2 : 1) * feePerByte;
        const total = ((fee*this.mercado/100000000)+(this.dolar*this.comision/100)).toFixed(2);
        const comis = ((this.dolar*this.comision/100)).toFixed(2);
        if (this.dolar>0 && caso == 'total'){
          return total;
        } else if (this.dolar>0 && caso == 'comision'){
          return comis;
        } else if (this.dolar>0 && caso == 'fee'){
          return fee;
        } else {
          return 0;
        }   
      }
    },
    CalculoMercado() {   
      Bitcoin.currentprice().then(result => {
          const btc = parseFloat((result.bpi.USD.rate).replace(',', ''));
          this.mercado  = ((btc*(100+this.porcentaje))/100).toFixed(4);
      });
    }










  }
};
</script>
