<template>
  <div>
    {{error}}
    <!--Stats cards-->
    <div class="row">
      <div class="col-md-6 col-xl-3" v-for="stats in statsCards" :key="stats.title">
        <stats-card>
          <div class="icon-big text-center" :class="`icon-${stats.type}`" slot="header">
            <i :class="stats.icon"></i>
          </div>
          <div class="numbers" slot="content">
            
            {{stats.value}}
          </div>
          <div class="stats" slot="footer">
            {{stats.title}}
          </div>
        </stats-card>
      </div>
    </div>

    <!--Table-->
    <div class="row" v-if="!modificar.activo">
      
      <div class="col-12">
        <card :title="table1.title" :subTitle="table1.subTitle">
          <div slot="raw-content" class="table-responsive">
            <div class="col-md-12">
              <button type="button"  class="close"  @click="Editar()">
                <i class="ti-write"></i>
              </button>
            </div>

          <table class="table" :class="tableClass">
            <thead>
              <slot name="columns">
                <th v-for="column in table1.columns" :key="column">{{column}}</th>
              </slot>
            </thead>
            <tbody>
              <tr v-for="(item, index) in table1.data" :key="index">
                <slot :row="item">
              <td v-for="(column, index) in table1.columns"
                :key="index"
                v-if="hasValue(item, column)">
                {{itemValue(item, column)}}
              </td>        
              <td>
                <button type="button" aria-hidden="true" class="close" @click="Editar(index)">
                  <i class="ti-pencil"></i>
                </button>
              </td>
                </slot>
              </tr>
            </tbody>
          </table>
          </div>
        </card>
      </div>
    </div>

    <div class="row" v-else>
      <div class="col-12">
        <card class="card" title="Modificar Portafolio">           
          <div class="row" >
            <div class="col-md-12">
              <button type="button"  class="close"  @click="Cerrar()">
                <i class="ti-close"></i>
              </button>
            </div>
          </div>

          <form @submit.prevent>

          <div class="row">
            <div class="col-md-9">
              <fg-input type="text" label="Nombre" v-model="modificar.nombre">
              </fg-input>
            </div>
            <div class="col-md-3">
              <fg-input type="number" step="0.0001" label="Objetivo"
                  min="0"  v-model="modificar.objetivo">
              </fg-input>
            </div>
          </div>

          <div class="row">
            <div class="col-md-3">
              <fg-input type="date" label="F. Inicio" v-model="modificar.fecha_ini">
              </fg-input>
            </div>
            <div class="col-md-3">
              <fg-input type="date" label="F. Fin" v-model="modificar.fecha_fin">
              </fg-input>
            </div>
            <div class="col-md-3">
              <label>Etapa</label>
              <select v-model="modificar.etapa" class="form-control border-input">
                <option v-for="(item, key) in etapa" :value="key" >
                    {{item}}
                </option>
              </select>
            </div>
          </div>

          <div class="row">
            <div class="col-md-12">
              <fg-input type="text" label="Descripci√≥n" v-model="modificar.descripcion">
              </fg-input>
            </div>
          </div>
          </form>

          <div class="row" >
            <div class="col-md-12">
              <button type="button"  class="close"  @click="Guardar()">
                <i class="ti-save"></i>
              </button>
            </div>
          </div>


        </card>
      </div>
    </div>


  </div>
</template>
<script>
import API from "../plugins/globalApi";
import Mensaje from "../plugins/globalNotificaciones";

import { StatsCard, ChartCard } from "@/components/index";
import Chartist from 'chartist';

export default {
  components: {
    StatsCard,
    ChartCard
  },
  /**
   * Chart data used to render stats, charts. Should be replaced with server data
   */
  data() {
    return {
      error: null,
      table1: {
        title: "Portafolio de Inversiones",
        subTitle: "",
        columns: ["Id", "Nombre", "Objetivo", "Recaudado", "Invers", "Fecha","Estatus"],
        data: [],
        edicion: true
      },
      modificar: {
        activo: false,
        id: 0,
        nombre: null,
        descripcion: null,
        fecha_ini: null,
        fecha_fin: null,
        estatus: null,
        objetivo: 0,
        etapa: 0
      },
      statsCards: [
        {
          type: "warning",
          icon: "ti-image",
          title: "Inversiones Activas",
          value: "0"
        },
        {
          type: "success",
          icon: "ti-stats-up",
          title: "Inversiones en Ejecuci√≥n",
          value: "0"
        },
        {
          type: "danger",
          icon: "ti-pulse",
          title: "Rentabilidad Distribuida",
          value: "0"
        },
        {
          type: "info",
          icon: "ti-thumb-up",
          title: "Cantidad de Inversionistas",
          value: "0"
        }
      ],
      etapa: ['En Proceso','Completo','Cerrado'],
      tipo: ['info','success','warning']

    }
  },
  created () {
    if(localStorage.getItem('rol')==0){
      window.location.href = '#/inicio';
    }

      API({
        action:  'informacion',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password')
      }).then(response => {
        //this.error = response.data;
        this.statsCards[0].value = response.data.inversion_activas;
        this.statsCards[1].value = response.data.inversion_ejecucion;
        this.statsCards[2].value = 0;
        this.statsCards[3].value = response.data.inversionistas;
      });
    this.Mostrar();
  },
  computed: {
    tableClass() {
      return `table-${this.type}`;
    }
  },
  methods: {
    Mostrar() {
      API({
        action:  'portafolio',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password')
      }).then(response => {
        //this.error = response.data.portafolio;
        response.data.portafolio.forEach(element => {
          this.table1.data.push({
            id: element.id,
            nombre: element.titulo,
            descripcion: element.descripcion,
            meta: element.meta,
            objetivo: "$"+element.meta,
            recaudado: "$"+element.monto+' ('+Math.round((element.monto/element.meta)*100)+'%)',
            invers: element.invers,
            fecha_ini: element.fecha_ini,
            fecha_fin: element.fecha_fin,
            fecha: element.fecha_ini+' / '+element.fecha_fin,
            estatus: this.etapa[element.etapa],
            etapa: element.etapa
          });
        });
      });
    },
    hasValue(item, column) {
      return item[column.toLowerCase()] !== "undefined";
    },
    itemValue(item, column) {
      return item[column.toLowerCase()];
    },
    Editar(index) {
      this.modificar.activo = true;
      this.modificar.id = this.table1.data[index].id;
      this.modificar.nombre = this.table1.data[index].nombre;
      this.modificar.descripcion = this.table1.data[index].descripcion;
      this.modificar.fecha_ini = this.table1.data[index].fecha_ini;
      this.modificar.fecha_fin = this.table1.data[index].fecha_fin;
      this.modificar. estatus = this.table1.data[index].estatus;
      this.modificar.objetivo = this.table1.data[index].meta;
      this.modificar.etapa = this.table1.data[index].etapa;
    },
    Guardar() {
      API({
        action:  'updateportafolio',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password'),
        id: this.modificar.id,
        nombre: this.modificar.nombre,
        descripcion: this.modificar.descripcion,
        fecha_ini: this.modificar.fecha_ini,
        fecha_fin: this.modificar.fecha_fin,
        meta: this.modificar.objetivo,
        etapa:  this.modificar.etapa
      }).then(response => {
        this.error = response.data;
        if (response.data.error){
          this.$notify(Mensaje(3,response.data.message));
        } else {
          this.$notify(Mensaje(2,response.data.message));
          this.table1.data = [];
          this.Mostrar();
        }   
      });
    },
    Cerrar() {
      this.modificar.activo = false;
      this.modificar.id = 0;
      this.modificar.nombre = null;
      this.modificar.descripcion = null;
      this.modificar.fecha_ini = null;
      this.modificar.fecha_fin = null;
      this.modificar.estatus = null;
      this.modificar.objetivo = 0;
      this.modificar.etapa = 0;
    }



    
  }
};
</script>
